header { background: #fff; width: 100%; height: 80px; min-width: 320px; text-align: left; order: 1; -webkit-order: 1; position: relative; }

header:after { content: ''; width: 100%; height: 241px; display: block; background-image: url(../img/main-bg.png); background-position: center bottom; background-size: 100% auto; background-repeat: no-repeat; position: absolute; z-index: -1; }

.header__container { max-width: 1200px; width: 100%; height: 100%; margin: 0 auto; position: relative; }

nav { height: 100%; }

.menu { display: flex; justify-content: center; align-items: center; list-style: none; width: 100%; height: 100%; }

.menu li { margin-left: 75px; margin-top: 2px; }

.menu li:first-child { margin-left: 0; }

.menu__link { font-size: 14px; color: #435993; font-weight: 300; transition: all 0.5s linear; }

.menu__link:hover { color: #232f4d; }

.divide { display: none; padding: 20px; position: absolute; right: 0; top: 0; }

.mobile { width: 100%; height: 100%; display: none; background: #fff; position: fixed; top: 0; left: 0; z-index: 100; }

.mobile__container { position: relative; }

.close { position: absolute; left: 0; right: 0; margin: auto; display: block; margin: 0 auto; text-align: center; padding: 20px; }

.close span { font-size: 20px; }

.navigation { padding-top: 100px; }

.navigation li { text-align: center; margin-bottom: 16px; }

.navigation__link { font-size: 20px; color: #1c1c1c; }

@media screen and (max-width: 1700px) { header:after { background-position: center top; } }

@media screen and (max-width: 768px) { header { height: 55px; }
  nav { display: none; }
  .divide { display: block; } }

.main-wrapper { padding: 0 0 0 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: column; -webkit-flex-direction: column; -ms-flex-direction: column; justify-content: flex-start; -webkit-justify-content: flex-start; -ms-flex-pack: start; }

.wrapper { min-width: 320px; max-width: 808px; padding: 0 20px 0 20px; margin: 0 auto; position: relative; }

/* titles */
p { font-size: 13px; line-height: 18px; color: #000; padding: 15px 0 15px 0; font-weight: 400; }

h1 { font-size: 36px; line-height: 42px; color: #000; font-family: "Roboto", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h2 { font-size: 32px; line-height: 37px; color: #000; font-family: "Roboto", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h3 { font-size: 28px; line-height: 32px; color: #000; font-family: "Roboto", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

/* loader */
.loaded .main-wrapper { visibility: hidden; }

.icon-load { background: url(../img/loader.gif) no-repeat left top; width: 40px; height: 40px; position: fixed; left: 50%; top: 50%; margin-left: -20px; margin-left: -20px; display: none; }

.loaded .icon-load { display: block; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; padding-bottom: 100px; }

.main-title { font-size: 70px; line-height: 75px; font-weight: 100; text-align: center; margin-top: 118px; padding: 0; }

.title { margin-top: 62px; }

.title__icon { display: block; margin: 0 auto; }

.title__value { font-size: 30px; font-weight: 300; text-align: center; margin-top: 27px; }

.title__note { max-width: 505px; width: 100%; margin: 15px auto 0 auto; font-size: 16px; line-height: 22px; font-weight: 300; text-align: center; }

.title.mini { margin-top: 36px; }

.main-form { margin-top: 47px; }

.inputs { margin-bottom: 72px; }

.inputs input { max-width: 520px; width: 100%; height: 60px; display: block; margin: 0 auto 25px auto; border-style: solid; border-width: 1px; border-color: #e5e5e5; border-radius: 5px; padding: 2px 12px 0 12px; text-align: center; }

.inputs input::-webkit-input-placeholder { font-size: 24px; font-weight: 300; text-align: center; color: #c8c8c8; }

.inputs input:-ms-input-placeholder { font-size: 24px; font-weight: 300; text-align: center; color: #c8c8c8; }

.inputs input::-ms-input-placeholder { font-size: 24px; font-weight: 300; text-align: center; color: #c8c8c8; }

.inputs input::placeholder { font-size: 24px; font-weight: 300; text-align: center; color: #c8c8c8; }

.select-container { max-width: 324px; width: 100%; height: 60px; margin: 0 auto 26px auto; }

.checkbox { width: 100%; margin-top: 16px; display: flex; margin-left: -1px; cursor: pointer; }

.style-checkbox { display: none; }

.style-checkbox:checked ~ .custom-checkbox:after { background: #435993; }

.custom-checkbox { width: 19px; height: 19px; border: 1px solid #435993; border-radius: 2px; margin-right: 12px; position: relative; }

.custom-checkbox:after { content: ''; position: absolute; left: 0; right: 0; top: 0; bottom: 0; transition: all  0.3s linear; }

.custom-checkbox:before { content: "\e91b"; color: #fff; font-size: 10px; font-family: 'icon-moon'; position: absolute; left: 2px; top: 4px; z-index: 50; }

.checkbox-text { width: calc(100% - 26px); font-size: 16px; line-height: 18px; color: #000; }

.checkbox-list { display: flex; flex-wrap: wrap; justify-content: space-between; margin-top: 32px; margin-bottom: 134px; }

.checkbox-element { width: 27%; }

.subtitles { width: 100%; display: flex; justify-content: space-between; align-items: flex-end; position: absolute; top: 8px; }

.subtitles__cell2 { margin-left: 52px; }

.submit-block { padding-top: 37px; }

.submit-block textarea { max-width: 525px; width: 100%; height: 180px; display: block; margin: 0 auto; border-style: solid; border-width: 1px; border-color: #e5e5e5; border-radius: 5px; padding: 21px 12px 0 24px; resize: none; font-size: 20px; font-weight: 300; color: #c8c8c8; }

.submit-block textarea::-webkit-input-placeholder { font-size: 20px; font-weight: 300; color: #c8c8c8; }

.submit-block textarea:-ms-input-placeholder { font-size: 20px; font-weight: 300; color: #c8c8c8; }

.submit-block textarea::-ms-input-placeholder { font-size: 20px; font-weight: 300; color: #c8c8c8; }

.submit-block textarea::placeholder { font-size: 20px; font-weight: 300; color: #c8c8c8; }

.submit-block input[type=submit] { width: 116px; height: 51px; border-radius: 10px; background-color: #435993; border: transparent; font-size: 16px; color: #fff; display: block; margin: 70px auto 0 auto; transition: all 0.5s linear; }

.submit-block input[type=submit]:hover { background-color: #334470; }

@media screen and (max-width: 1100px) { .main-title { margin-top: 60px; } }

@media screen and (max-width: 1024px) { .title { margin-top: 25px; } }

@media screen and (max-width: 640px) { .subtitles { font-size: 12px; top: -2px; } }

@media screen and (max-width: 600px) { .xdsoft_legend { visibility: visible; }
  .subtitles { display: none; }
  .checkbox-element { width: 50%; }
  .title__value { font-size: 25px; }
  .submit-block input[type=submit] { margin-top: 35px; }
  .inputs input::-webkit-input-placeholder { font-size: 16px; font-weight: 300; color: #c8c8c8; }
  .inputs input:-ms-input-placeholder { font-size: 16px; font-weight: 300; color: #c8c8c8; }
  .inputs input::-ms-input-placeholder { font-size: 16px; font-weight: 300; color: #c8c8c8; }
  .inputs input::placeholder { font-size: 16px; font-weight: 300; color: #c8c8c8; }
  .jq-selectbox__select-text { font-size: 16px; }
  .submit-block textarea::-webkit-input-placeholder { font-size: 16px; font-weight: 300; color: #c8c8c8; }
  .submit-block textarea:-ms-input-placeholder { font-size: 16px; font-weight: 300; color: #c8c8c8; }
  .submit-block textarea::-ms-input-placeholder { font-size: 16px; font-weight: 300; color: #c8c8c8; }
  .submit-block textarea::placeholder { font-size: 16px; font-weight: 300; color: #c8c8c8; } }

@media screen and (max-width: 375px) { .checkbox-element { width: 100%; } }
